<template>
  <div class="cell_wrap" @click="$router.push(`/article/${artObj.art_id}`)">
    <van-cell >
      <template #title>
        <div class="title-box">
          <span>{{ artObj.title }}</span>
          <!-- 单图 -->
          <img
            v-lazy="artObj.cover.images[0]"
            v-if="artObj.cover.type === 1"
            class="thumb"
          />
        </div>
        <!-- 三图  另起一行 -->
        <div class="thumb-box" v-if="artObj.cover.type > 1">
          <img
            v-lazy="str"
            v-for="(str, index) in artObj.cover.images"
            :key="index"
            class="thumb"
          />
        </div>
      </template>
      <template #label>
        <div class="label-box">
          <span>
            {{ artObj.aut_name }}&nbsp;&nbsp;&nbsp;
            {{ artObj.comm_count }}评论&nbsp;&nbsp;&nbsp;{{
              $changeTime(artObj.pubdate)
            }}
          </span>
          <van-icon name="cross" @click.stop="show = true" v-if="closeShow" />
          <van-action-sheet
            v-model="show"
            get-container="body"
            cancel-text="取消"
            :actions="actions"
            @select="onSelect"
            @closed="onClosed"
          />
        </div>
      </template>
    </van-cell>
  </div>
</template>

<script>
export default {
  props: {
    artObj: Object,
    closeShow: {
      type: Boolean, // 类型
      default: true // 默认值
    }
  },
  data () {
    return {
      show: false, // 控制反馈弹出
      actions: [], // 显示反馈的选项
      newActions: [// 一级反馈内容
        { name: '不感兴趣' },
        { name: '反馈垃圾内容' },
        { name: '拉黑作者' }
      ]
    }
  },
  created () {
    this.actions = this.newActions
  },
  methods: {
    onSelect (action) { // 二级反馈菜单内容
      if (action.name === '反馈垃圾内容') {
        this.actions = [
          {
            value: -1,
            name: '返回'
          },
          {
            value: 0,
            name: '其它问题'
          },
          {
            value: 1,
            name: '标题夸张'
          },
          {
            value: 2,
            name: '低俗色情'
          },
          {
            value: 3,
            name: '错别字多'
          },
          {
            value: 4,
            name: '旧闻重复'
          },
          {
            value: 6,
            name: '内容不实'
          },
          {
            value: 8,
            name: '侵权'
          },
          {
            value: 5,
            name: '广告软文'
          },
          {
            value: 7,
            name: '涉嫌违法犯罪'
          }
        ]
      } else if (action.name === '返回') {
        this.actions = this.newActions
      } else {
        this.$emit('del', this.artObj.art_id, action.value)
      }
    },
    onClosed () {
      this.actions = this.newActions
    }
  }
}
</script>

<style scoped lang="less">
/* .cell_wrap /deep/ .van-icon-cross{
    display: flex;
    align-items: flex-end;
    padding-bottom: 2px;
} */
.title-box {
  // 标题样式
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.label-box {
  // label描述样式
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.thumb {
  // 图片样式
  // 矩形黄金比例：0.618
  width: 113px;
  height: 70px;
  background-color: #f8f8f8;
  object-fit: cover;
}

.thumb-box {
  // 三图容器
  display: flex;
  justify-content: space-between;
}
</style>
