<template>
  <div>
    <!-- Header 区域 -->
    <van-nav-bar
      fixed
      title="文章详情"
      left-arrow
      @click-left="$router.back()"
    />

    <!-- 文章信息区域 -->
    <div class="article-container">
      <!-- 文章标题 -->
      <h1 class="art-title">{{ articleObj.title }}</h1>

      <!-- 用户信息 -->
      <van-cell
        center
        :title="articleObj.aut_name"
        :label="$changeTime(articleObj.pubdate)"
      >
        <template #icon>
          <img :src="articleObj.aut_photo" alt="" class="avatar" />
        </template>
        <template #default>
          <div>
            <van-button
              v-if="articleObj.is_followed"
              type="info"
              size="mini"
              @click="followFn(false)"
              >已关注</van-button
            >
            <van-button
              v-else
              icon="plus"
              type="info"
              size="mini"
              plain
              @click="followFn(true)"
              >关注</van-button
            >
          </div>
        </template>
      </van-cell>

      <!-- 分割线 -->
      <van-divider></van-divider>

      <!-- 文章内容 -->
      <div class="art-content" v-html="articleObj.content"></div>

      <!-- 分割线 -->
      <van-divider>End</van-divider>

      <!-- 点赞 -->
      <div class="like-box">
        <van-button
          icon="good-job"
          type="danger"
          size="small"
          v-if="articleObj.attitude && articleObj.attitude !== -1"
          @click="attitudeFn(false)"
          >已点赞</van-button
        >
        <van-button
          v-else
          icon="good-job-o"
          type="danger"
          plain
          size="small"
          @click="attitudeFn(true)"
          >点赞</van-button
        >
      </div>
    </div>

    <!--评论列表  -->
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
    >
      <Comment :comList="commentList"></Comment>
    </van-list>
    <div :class="comShow ? 'art-cmt-container-1' : 'art-cmt-container-2'">
      <!-- 底部添加评论区域 - 1 -->
      <div class="add-cmt-box van-hairline--top" v-if="comShow">
        <van-icon name="arrow-left" size="0.48rem" @click="$router.back()" />
        <div class="ipt-cmt-div" @click="comShow = false">发表评论</div>
        <div class="icon-box">
          <van-badge :content="totalCount > 0 ? totalCount : ' '" max="99">
            <van-icon
              name="comment-o"
              size="0.53333333rem"
              @click="comScrollFn"
            />
          </van-badge>
          <van-icon name="star-o" size="0.53333333rem" />
          <van-icon name="share-o" size="0.53333333rem" />
        </div>
      </div>

      <!-- 底部添加评论区域 - 2 -->
      <div class="cmt-box van-hairline--top" v-else>
        <textarea
          placeholder="友善评论、理性发言、阳光心灵"
          v-focus
          @blur="blurFn"
          v-model.trim="comText"
        ></textarea>
        <van-button type="default" :disabled="comText.length===0" @click="sendComFn">发布</van-button>
      </div>
    </div>
  </div>
</template>

<script>
import {
  artilceDetailAPI,
  autFollowedAPI,
  unFollowedAPI,
  artLikeAPI,
  unLikeAPI,
  getCommentAPI,
  sendCommentAPI
} from '@/api'
import Comment from './Comment'
export default {
  data () {
    return {
      articleObj: {}, // 文章详情
      commentList: [], // 评论列表
      comShow: true, // 控制发表评论区域显示
      totalCount: 0, // 评论总数
      loading: false,
      finished: false,
      lastId: null, // 根据时间戳获取加载数据
      comText: '' // 发表评论的内容
    }
  },
  async created () {
    // 文章详情获取
    const [, res] = await artilceDetailAPI(this.$route.params.id)
    this.articleObj = res.data.data
  },
  methods: {
    blurFn () {
      setTimeout(() => { // 失去焦点便会删除标签，解决拿不到文本域中数据的问题： 1，定时器 2.把v-if改为v-show
        this.comShow = true
      }, 0)
    },
    async getComment () { // 获取文章评论
      const [, res] = await getCommentAPI({
        type: 'a',
        source: this.$route.params.id,
        offset: this.lastId // 开始传的是null，axios会自动忽略这一对键值对
      })
      if (res?.data?.data?.results.length === 0) {
        this.finished = true // 没有评论  停止onload加载
        return
      }
      this.commentList = [...this.commentList, ...res.data.data.results] // 评论列表
      this.totalCount = res.data.data.total_count // 评论总数
      this.lastId = res.data.data.last_id// lastid用来获取下一个时间戳的数据
      this.loading = false // 加载完关闭加载按钮   再次触发再加载
    },
    async followFn (bool) { // 关注作者
      // 控制是否已关注
      this.articleObj.is_followed = bool
      if (bool === true) {
        const [err] = await autFollowedAPI({
          // 关注作者
          target: this.articleObj.aut_id
        })
        if (!err) this.$Toast({ message: '关注成功' })
      } else {
        const [err] = await unFollowedAPI({
          // 取消关注
          target: this.articleObj.aut_id
        })
        if (!err) this.$Toast({ message: '取消成功' })
      }
    },
    async attitudeFn (bool) { // 喜欢文章
      // 控制是否喜欢文章
      this.articleObj.attitude = bool
      if (bool === true) {
        const [err] = await artLikeAPI({
          target: this.articleObj.art_id
        })
        if (!err) this.$Toast({ message: '点赞成功' })
      } else {
        const [err] = await unLikeAPI(this.$route.params.id)
        if (!err) this.$Toast({ message: '取消成功' })
      }
    },
    comScrollFn () {
      // 点击评论按钮  实现页面滑动评论列表
      const now = window.scrollY // 起始滚动距离  0
      let dist = document.querySelector('.article-container').offsetHeight // 文章到顶部的高度
      if (dist > document.documentElement.scrollHeight - window.innerHeight) {
        dist = document.documentElement.scrollHeight - window.innerHeight
      }
      const step = (dist - now) / 10 // 控制每次滑动多少
      //   console.log(step)
      setTimeout(() => {
        if (Math.abs(step) < 1) {
          window.scrollTo(0, dist)
          return
        }
        window.scrollTo(0, now + step)
        this.comScrollFn()
      }, 10)
    },
    onLoad () { // 加载评论数据
      this.getComment()
    },
    async sendComFn () { // 发表评论
      const [, res] = await sendCommentAPI({
        target: this.$route.params.id,
        content: this.comText
      })
      this.commentList.unshift(res.data.data.new_obj) // 新加评论添加到数组置顶
      this.totalCount++ // 控制微表变化
      this.comText = '' // 清空文本域
      console.log(this.commentList)
    }
  },
  components: {
    Comment
  }
}
</script>

<style scoped lang="less">
.article-container {
  padding: 10px;
  margin-top: 46px;
}
.art-title {
  font-size: 16px;
  font-weight: bold;
  margin: 10px 0;
}

.art-content {
  font-size: 12px;
  line-height: 24px;
  width: 100%;
  overflow-x: scroll;
  word-break: break-all;
}

.van-cell {
  padding: 5px 0;
  &::after {
    display: none;
  }
}

.avatar {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: #f8f8f8;
  margin-right: 5px;
  border: none;
}

.like-box {
  display: flex;
  justify-content: center;
}

/*美化 - 文章详情 - 底部的评论页面 */
// 外层容器
.art-cmt-container-1 {
  padding-bottom: 46px;
}
.art-cmt-container-2 {
  padding-bottom: 80px;
}

// 发布评论的盒子 - 1
.add-cmt-box {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  box-sizing: border-box;
  background-color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 46px;
  line-height: 46px;
  padding-left: 10px;
  .ipt-cmt-div {
    flex: 1;
    border: 1px solid #efefef;
    border-radius: 15px;
    height: 30px;
    font-size: 12px;
    line-height: 30px;
    padding-left: 15px;
    margin-left: 10px;
    background-color: #f8f8f8;
  }
  .icon-box {
    width: 40%;
    display: flex;
    justify-content: space-evenly;
    line-height: 0;
  }
}

.child {
  width: 20px;
  height: 20px;
  background: #f2f3f5;
}

// 发布评论的盒子 - 2
.cmt-box {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 12px;
  padding-left: 10px;
  box-sizing: border-box;
  background-color: white;
  textarea {
    flex: 1;
    height: 66%;
    border: 1px solid #efefef;
    background-color: #f8f8f8;
    resize: none;
    border-radius: 6px;
    padding: 5px;
  }
  .van-button {
    height: 100%;
    border: none;
  }
}
</style>
