<template>
<div class="user-container">
    <!-- 用户基本信息面板 -->
    <div class="user-card">
      <!-- 用户头像、姓名 -->
      <van-cell>
        <!-- 使用 title 插槽来自定义标题 -->
        <template #icon>
          <img :src="userObj.photo" alt="" class="avatar">
        </template>
        <template #title>
          <span class="username">{{userObj.name}}</span>
        </template>
        <template #label>
          <van-tag color="#fff" text-color="#007bff">申请认证</van-tag>
        </template>
      </van-cell>
      <!-- 动态、关注、粉丝 -->
      <div class="user-data">
        <div class="user-data-item">
          <span>{{userObj.art_count}}</span>
          <span>动态</span>
        </div>
        <div class="user-data-item">
          <span>{{userObj.follow_count}}</span>
          <span>关注</span>
        </div>
        <div class="user-data-item">
          <span>{{userObj.fans_count}}</span>
          <span>粉丝</span>
        </div>
      </div>
    </div>

    <!-- 操作面板 -->
    <van-cell-group class="action-card">
      <van-cell icon="edit" title="编辑资料" is-link to="/layout/my/edit" />
      <van-cell icon="chat-o" title="小思同学" is-link @click="$router.push({name:'Chat'})"/>
      <van-cell icon="warning-o" title="退出登录" is-link @click="quitFn"/>
    </van-cell-group>
  </div>
</template>

<script>
import { getUserInfoAPI } from '@/api'
import { mapMutations, mapState } from 'vuex'
import { clearStorage } from '@/storage'
export default {
  data () {
    return {

    }
  },
  computed: {
    ...mapState({
      userObj: state => state.userInfo
    })
  },
  async activated () {
    const [, res] = await getUserInfoAPI()
    this.setUserInfo(res.data.data)
  },
  methods: {
    ...mapMutations(['setUserInfo', 'delToken', 'delRefreshToken']),
    quitFn () {
      this.$Dialog({ title: '提示', message: '是否确定退出?', showCancelButton: true }).then(() => {
        this.delToken() // 清除vuex中地token
        this.delRefreshToken()// 清除vuex中refreshtoken
        clearStorage() // 清除本地存储token
        this.$router.replace({ name: 'Login' }) // 跳转到登录页
      }).catch(() => {})
    }
  }
}
</script>

<style scoped lang="less">
.user-container {
  .user-card {
    background-color: #007bff;
    color: white;
    padding-top: 20px;
    .van-cell {
      background: #007bff;
      color: white;
      &::after {
        display: none;
      }
      .avatar {
        width: 60px;
        height: 60px;
        background-color: #fff;
        border-radius: 50%;
        margin-right: 10px;
      }
      .username {
        font-size: 14px;
        font-weight: bold;
      }
    }
  }
  .user-data {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    font-size: 14px;
    padding: 30px 0;
    .user-data-item {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 33.33%;
    }
  }
}
</style>
