<template>
  <div>
    <!-- 我的频道 -->
    <div class="my-channel-box">
      <div class="channel-title">
        <span>我的频道</span>
        <span :style="{color:isShow? 'red':'green'}">{{isShow? '点击移除频道':'点击进入频道'}}</span>
        <span @click="isShow=!isShow">{{isShow? '完成':'编辑'}}</span>
      </div>
      <!-- 我的频道列表 -->
      <van-row type="flex">
        <van-col span="6" v-for="(obj,index) in userList" :key="obj.id" @click="changeChannelFn(index,obj)">
          <div class="channel-item van-hairline--surround">
            {{ obj.name }}
            <!-- 删除的徽标 -->
            <van-badge color="transparent" class="cross-badge" v-show="isShow" v-if="index!==0">
              <template #content>
                <van-icon
                  name="cross"
                  class="badge-icon"
                  color="#cfcfcf"
                  size="0.32rem"
                />
              </template>
            </van-badge>
          </div>
        </van-col>
      </van-row>
    </div>

    <!-- 更多频道 -->
    <div class="more-channel-box">
      <div class="channel-title">
        <span>点击添加更多频道：</span>
      </div>
      <!-- 更多频道列表 -->
      <van-row type="flex">
        <van-col span="6" v-for="obj in unList" :key="obj.id" @click="addChannelFn(obj)">
          <div class="channel-item van-hairline--surround">
            {{ obj.name }}
          </div>
        </van-col>
      </van-row>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      isShow: false
    }
  },
  props: {
    allList: Array,
    userList: Array,
    value: Number
  },
  //   通过全部频道和已选频道  计算剩余频道展示
  computed: {
    unList () {
      return this.allList.filter(obj1 => {
        const index = this.userList.findIndex(obj2 => obj1.id === obj2.id)
        return index === -1
      })
    }
  },
  methods: { // 删除已选状态的频道
    changeChannelFn (index, obj) {
      if (index !== 0 && this.isShow === true) {
        this.userList.splice(index, 1)
        this.$emit('updateChannel', this.userList)// 更新用户频道
      } else {
        this.$emit('input', obj.id) // 改变active影响对应tabs的数据响应
        this.$emit('closePop') // 让外部在选中频道后关闭弹出层
      }
    },
    addChannelFn (obj) { // 添加新的频道到已选
      this.userList.push(obj)
      this.$emit('updateChannel', this.userList)// 更新用户频道
    }
  }
}
</script>

<style scoped lang="less">
.van-popup,
.popup-container {
  background-color: transparent;
  height: 100%;
  width: 100%;
}

.popup-container {
  display: flex;
  flex-direction: column;
}

.pop-header {
  height: 90px;
  background-color: #007bff;
  color: white;
  text-align: center;
  font-size: 14px;
  position: relative;
  .title {
    width: 100%;
    position: absolute;
    bottom: 15px;
  }
}

.pop-body {
  flex: 1;
  overflow: scroll;
  padding: 8px;
  background-color: white;
}

.my-channel-box,
.more-channel-box {
  .channel-title {
    display: flex;
    justify-content: space-between;
    font-size: 14px;
    line-height: 28px;
    padding: 0 6px;
  }
}

.channel-item {
  font-size: 12px;
  text-align: center;
  line-height: 36px;
  background-color: #fafafa;
  margin: 5px;
}

/*删除的微标 */
.cross-badge {
  position: absolute;
  right: -3px;
  top: 0;
  border: none;
}

/*提示文字 */
.small_title {
  font-size: 12px;
  color: gray;
}
</style>
