<template>
  <div>
    <ArticleItem
      v-for="obj in articleList"
      :key="obj.art_id"
      :artObj="obj"
      @del="delFn"
    ></ArticleItem>

  </div>
</template>

<script>
import ArticleItem from '@/components/ArticleItem'
export default {

  props: {
    articleList: Array
  },
  components: {
    ArticleItem
  },
  methods: {
    delFn (art_id, type) {
      this.$emit('del', art_id, type)
    }
  }
}
</script>

