<template>
  <div>
    <!-- 评论列表 -->
    <div class="cmt-list">
      <!-- 评论的 Item 项 -->
      <div class="cmt-item" v-for="obj in comList" :key="obj.com_id">
        <!-- 头部区域 -->
        <div class="cmt-header">
          <!-- 头部左侧 -->
          <div class="cmt-header-left">
            <img :src="obj.aut_photo" alt="" class="avatar">
            <span class="uname">{{obj.aut_name}}</span>
          </div>
          <!-- 头部右侧 -->
          <div class="cmt-header-right">
            <van-icon v-if="obj.like_count===1" name="like" size="16" color="red" @click="loveFn(obj,0)"/>
            <van-icon v-else name="like-o" size="16" color="gray" @click="loveFn(obj,1)"/>
          </div>
        </div>
        <!-- 主体区域 -->
        <div class="cmt-body" >
            {{obj.content}}
        </div>
        <!-- 尾部区域 -->
        <div class="cmt-footer">{{$changeTime(obj.pubdate)}}</div>
      </div>
    </div>
  </div>
</template>

<script>
import { likeCommentAPI, unLikeCommentAPI } from '@/api'
export default {
  props: {
    comList: {
      type: Array,
      default () {
        return []
      }
    }
  },
  methods: {
    async loveFn (obj, bool) { // 控制小红心
      obj.like_count = bool
      if (bool === true) {
        const [err] = await likeCommentAPI({ // 点赞成功
          target: obj.com_id
        })
        if (!err) this.$Toast({ message: '点赞成功' })
      } else { // 取消点赞
        const [err] = await unLikeCommentAPI(obj.com_id)
        if (!err) this.$Toast({ message: '取消成功' })
      }
    }
  }
}
</script>

<style scoped lang="less">
.cmt-list {
  padding: 10px;
  .cmt-item {
    padding: 15px 0;
    + .cmt-item {
      border-top: 1px solid #f8f8f8;
    }
    .cmt-header {
      display: flex;
      align-items: center;
      justify-content: space-between;
      .cmt-header-left {
        display: flex;
        align-items: center;
        .avatar {
          width: 40px;
          height: 40px;
          background-color: #f8f8f8;
          border-radius: 50%;
          margin-right: 15px;
        }
        .uname {
          font-size: 12px;
        }
      }
    }
    .cmt-body {
      font-size: 14px;
      line-height: 28px;
      text-indent: 2em;
      margin-top: 6px;
      word-break: break-all;
    }
    .cmt-footer {
      font-size: 12px;
      color: gray;
      margin-top: 10px;
    }
  }
}
</style>
