<template>
  <div class="search-result-container">
    <!-- 点击实现后退效果 -->
    <van-nav-bar
      title="搜索结果"
      left-arrow
      @click-left="$router.go(-1)"
      fixed
    />
    <!-- 搜索结果展示 -->
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
      offset="10"
    >
      <ArticleItem
      v-for="obj in resultList"
      :key="obj.art_id"
      :artObj="obj"
      :closeShow="false"
    ></ArticleItem>
    </van-list>

  </div>
</template>

<script>
import { searchResultAPI } from '@/api'
import ArticleItem from '@/components/ArticleItem'
export default {
  name: 'SearchResult',
  data () {
    return {
      resultList: [], // 搜索结果
      loading: false,
      finished: false,
      page: 1 // 控制搜索页码
    }
  },
  components: {
    ArticleItem
  },
  methods: {
    async getSearchResult () {
      // 获取搜索结果
      const [, res] = await searchResultAPI({
        q: this.$route.params,
        page: this.page
      })
      if (res.data?.data?.results?.length === 0) {
        this.finished = true // 全部加载完毕(无更多数据了) - list也不会再触发onload了
        return
      }
      this.resultList = [...this.resultList, ...res.data.data.results]
      this.loading = false
    },
    onLoad () { // 页面打开时  自动触发
      this.getSearchResult()
      this.page++
    }
  }
}
</script>

<style lang="less" scoped>
.search-result-container {
  padding-top: 46px;
}
</style>
