import axios from '@/utils/request'

// 获取用户已选频道
export const userChannel = () => axios({
  url: '/v1_0/user/channels'

})

// 获取全部频道
export const allChannel = () =>
  axios({
    url: '/v1_0/channels'

  })

// 频道更新
export const undateChannel = data =>
  axios({
    url: '/v1_0/user/channels',
    method: 'PUT',
    data
  })

// 获取文章
export const getArticleList = params => axios({
  url: '/v1_1/articles',
  params
})

// 删除不感兴趣
export const dislike = data =>
  axios({
    url: '/v1_0/article/dislikes',
    method: 'POST',
    data
  })

// 反馈--举报
export const report = data =>
  axios({
    url: '/v1_0/article/reports',
    method: 'POST',
    data
  })
