<template>
  <div>
    <div class="search-header">
      <!-- 后退按钮 -->
      <van-icon
        name="arrow-left"
        color="white"
        size="18"
        class="goback"
        @click="$router.go(-1)"
      />
      <!-- 搜索组件 -->
      <van-search
        v-model.trim="kw"
        placeholder="请输入搜索关键词"
        background="#007BFF"
        shape="round"
        v-focus
        ref="search"
      />
    </div>
    <!-- 搜索建议列表 -->
    <div class="sugg-list" v-if="suggestList.length!==0">
      <div
        class="sugg-item"
        v-for="(item, i) in suggestList"
        :key="i"
        v-html="lightItem(item)"
        @click="$router.push({path:`/search_result/${item}`})"
      ></div>
    </div>
    <!-- 搜索历史 -->
    <div class="search-history" v-else>
      <!-- 标题 -->
      <van-cell title="搜索历史">
        <!-- 使用 right-icon 插槽来自定义右侧图标 -->
        <template #right-icon>
          <van-icon name="delete" class="search-icon" @click="clearHistoryFn"/>
        </template>
      </van-cell>

      <!-- 历史列表 -->
      <div class="history-list" >
        <span class="history-item" v-for="(tag, i) in history" :key="i" @click="localFn(tag)" >{{
          tag
        }}</span>
      </div>
    </div>
  </div>
</template>

<script>
import { searchSuggestAPI } from '@/api'
import { setStorage, getStorage } from '@/storage/local'
export default {
  data () {
    return {
      kw: '', // 搜索关键字
      timer: '', // 定时器
      suggestList: [], // 建议列表
      history: JSON.parse(getStorage('history')) || [] // 搜索历史
    }
  },
  watch: {
    kw () { // 侦听输入框中  值的变化
      clearTimeout(this.timer) // 清除定时器
      if (this.kw.length === 0) {
        // 输入为空时  不发送请求
        this.suggestList = []
        return
      }
      this.timer = setTimeout(async () => {
        // 追加搜索历史记录显示  添加时  去重
        // 数组去重  方法一：
        this.history = Array.from(new Set([...this.history, this.kw]))
        // 数组去重  方法二：
        // if (this.history.indexOf(this.kw) === -1) {
        //   this.history.push(this.kw)
        // }
        // 输入框防抖实现   多久执行一次
        const [, res] = await searchSuggestAPI({
          q: this.kw
        })
        this.suggestList = res.data.data.options
      }, 500)
    },
    history: { // 深度侦听，保存到本地
      deep: true,
      handler () {
        setStorage('history', JSON.stringify(this.history))
      }
    }
  },
  methods: {
    lightItem (item) { // 关键字高亮
      const reg = new RegExp(this.kw, 'ig') // 正则判断   i忽略大小写 g全局匹配
      return item.replace(reg, val => {
        // val值为匹配的字符
        return `<span style="color:red">${val}</span>`
      })
    },
    localFn (tag) { // 点击历史记录  提交到输入框中  自动聚焦
      this.kw = tag
      this.$refs.search.querySelector('input').focus()
    },
    clearHistoryFn () { // 删除历史记录
      this.history = []
    }
  }
}
</script>

<style scoped lang="less">
.search-header {
  height: 46px;
  display: flex;
  align-items: center;
  background-color: #007bff;
  overflow: hidden;
  /*后退按钮*/
  .goback {
    padding-left: 14px;
  }
  /*搜索组件*/
  .van-search {
    flex: 1;
  }
}
/*搜索建议列表样式 */
.sugg-list {
  .sugg-item {
    padding: 0 15px;
    border-bottom: 1px solid #f8f8f8;
    font-size: 14px;
    line-height: 50px;
    // 实现省略号的三行代码
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}
/**搜索历史 */
.search-icon {
  font-size: 16px;
  line-height: inherit;
}

.history-list {
  padding: 0 10px;
  .history-item {
    display: inline-block;
    font-size: 12px;
    padding: 8px 14px;
    background-color: #efefef;
    margin: 10px 8px 0px 8px;
    border-radius: 10px;
  }
}
</style>
