import axios1 from 'axios'
import to from '@/utils/awaitTo' // 把to函数请过来-让它包装住axios请求, 把结果处理成数组再返回到业务页面中
import store from '@/store'
import router from '@/router'
import Notify from '@/ui/Notify'
import { removeStorage } from '@/storage'
import { refreshTokenAPI } from '@/api'

var JSONbigString = require('json-bigint')({ storeAsString: true })
const axios = axios1.create({
  baseURL: 'http://toutiao-app.itheima.net', // 请求的基础路径
  transformResponse: [
    function (data) {
      if (data.length !== 0) {
        return JSONbigString.parse(data)
      } else {
        return data
      }
    }

  ]
})

// 添加请求拦截器
axios.interceptors.request.use(function (config) {
  // 在发送请求之前做些什么
  if (store.state.token) {
    config.headers.Authorization = 'Bearer ' + store.state.token
  }
  return config
}, function (error) {
  // 对请求错误做些什么

  return Promise.reject(error)
})

// 添加响应拦截器
axios.interceptors.response.use(function (response) {
  // 对响应数据做点什么
  return response
}, async function (error) {
  // 对响应错误做点什么
  if (error?.response?.status === 401) {
    // 无权限 - 处理
    removeStorage('token')
    store.commit('delToken')

    // token过期方式1: 跳转到登录页, 重新登录, 重新拿到token值
    // Notify({ type: 'danger', message: 'token过期-请重新登录' })
    // setTimeout(() => {
    //   router.push({
    //     name: 'Login'
    //   })
    // }, 1000) // 给用户500毫米反应

    // token过期处理方式2: 重新请求新的token  (token续签)
    if (store.state.refresh_token === null) {
      // 证明没有refresh_token
      Notify({ type: 'danger', message: '请登录' })
      setTimeout(() => {
        router.push({
          name: 'Login'
        })
      }, 1000)
    } else {
      const [err, res] = await refreshTokenAPI() // 用refresh_token重新获取token时
      if (err?.response?.status === 403) {
        // refresh_token(过期了/错误)
        Notify({ type: 'danger', message: '请重新请登录' })
        setTimeout(() => {
          router.push({
            name: 'Login'
          })
        }, 1000)
      } else {
        // refresh_token正常, 拿到正常的新的token
        // 把新的token保存到本地和vuex中
        store.commit('setToken', res.data.data.token)
        // 虽然你把新的token拿回来了, 但是上一次请求时是401, 成功了吗?
        // 再无感知重新请求一遍
        console.dir(error)
        error.config.headers.Authorization = res.data.data.token // 把新的token带上
        return axios(error.config) // 发送一次未完成的请求
      }
    }
  }
  return Promise.reject(error)
})

// axios.defaults.baseURL = 'http://toutiao-app.itheima.net'
// 另一个服务器
// axios.defaults.baseURL = 'http://api-toutiao-web.itheima.net/app'
export default ({ url, method = 'GET', data = {}, headers = {}, params = {} }) => {
  const PromiseA = axios({
    url,
    method,
    data,
    headers,
    params
  })
  return to(PromiseA) // 开整-网络请求
}
