<template>
  <div>
    <!-- 首页头部 -->
    <van-nav-bar fixed ref="nav">
      <template #left>
        <van-image
          class="logo"
          :src="require('../../assets/toutiao_logo.png')"
        />
        <!-- <img src="../../assets/toutiao_logo.png" alt=""> -->
      </template>
      <template #right>
        <van-icon
          name="search"
          color="white"
          size="0.48rem"
          @click="$router.push({ name: 'Search' })"
        />
      </template>
    </van-nav-bar>
    <!-- tab页导航和对应内容 -->
    <div class="top">
      <!-- @click="tabFn" -->
      <van-tabs v-model="active" sticky offset-top="1.22666667rem">
        <van-tab
          :title="obj.name"
          v-for="obj in userList"
          :key="obj.id"
          :name="obj.id"
        >
          <van-pull-refresh
            v-model="loading"
            @refresh="onRefresh"
            loading-text=" "
          >
            <van-list
              v-model="isLoading"
              :finished="finished"
              finished-text="没有更多了"
              @load="onLoad"
              offset="10"
            >
              <ArticleList :articleList="articleList" @del="delFn"></ArticleList
            ></van-list>
          </van-pull-refresh>
        </van-tab>
      </van-tabs>
    </div>
    <!-- 添加频道按钮 + -->
    <van-icon
      name="plus"
      size="0.37333333rem"
      class="moreChannels"
      @click="show = true"
    />
    <van-popup v-model="show" :close-on-click-overlay="false" class="edit_wrap">
      <!-- 弹出层的头部区域 -->
      <van-nav-bar title="频道管理">
        <template #right>
          <van-icon
            name="cross"
            size="0.37333333rem"
            color="white"
            @click="closeEdit"
          />
        </template>
      </van-nav-bar>
      <!-- 弹出层主体区域 -->
      <ChannelEdit
        v-model="active"
        ref="channel"
        :allList="allList"
        :userList="userList"
        @updateChannel="updateChannelFn"
        @closePop="show = false"
      ></ChannelEdit>
    </van-popup>
  </div>
</template>

<script>
import {
  userChannelAPI,
  allChannelAPI,
  undateChannelAPI,
  getArticleListAPI,
  dislikeAPI,
  reportAPI
} from '@/api'
import ArticleList from './components/ArticleList'
import { Toast } from 'vant'
import ChannelEdit from './ChannelEdit'
export default {
  components: {
    ArticleList, // 文章列表
    ChannelEdit // 编辑频道
  },
  data () {
    return {
      active: 0, // 用户已选对应的id
      userList: [], // 用户已选频道
      allList: [], // 全部频道
      articleList: [], // 文章列表
      channelShow: 0, // 文章id
      isLoading: false, // 上拉刷新状态
      finished: false, // 完成
      loading: false, // 下拉刷新状态
      page: 1, // 刷新使用的页码
      show: false // 控制弹出层是否显示
    }
  },
  async created () {
    // 获取用户已选频道
    const [, res] = await userChannelAPI()
    this.userList = res.data.data.channels
    // 获取全部频道
    const [, res2] = await allChannelAPI()
    this.allList = res2.data.data.channels
    // 调用获取文章
    // this.getArticleList()  //侦听解决
  },
  methods: {
    // 封装获取文章请求
    async getArticleList () {
      // 获取文章
      const [, article_res] = await getArticleListAPI({
        channel_id: this.channelShow,
        timestamp: new Date().getTime().toString(),
        // 切换另一个服务器的配置
        // timestamp: 1556789000001,
        with_top: 1,
        page: this.page
      })
      if (article_res.data.data?.results.length === 0) {
        this.finished = true
        return
      }
      //   console.log(article_res)
      this.articleList = [
        ...this.articleList,
        ...article_res.data.data.results
      ]
      this.isLoading = false
      this.loading = false
      // 加载完关闭请提示
      Toast.clear()
    },
    onRefresh () {
      // 下拉刷新
      this.isLoading = false // 消除加载圈圈
      this.page = 1
      this.articleList = [] // 清空文章列表  重新加载
      Toast.loading({
        message: '加载中...',
        forbidClick: true,
        duration: 0 // 加载提示  在加载完关闭  默认2秒   若请求超过2秒  提示框自动关闭
      })
      this.getArticleList()
    },
    onLoad () {
      // 加载更多
      this.getArticleList()
      this.page++
    },
    async delFn (art_id, type) {
      // 删除不感兴趣和举报
      const index = this.articleList.findIndex(obj => obj.art_id === art_id)
      this.articleList.splice(index, 1)
      if (type === undefined) {
        await dislikeAPI({
          // 删除不感兴趣
          target: art_id
        })
      } else {
        await reportAPI({
          // 指定举报--原因
          target: art_id,
          type: type
        })
      }

      this.articleList = []
      this.page = 1
      this.getArticleList()
    },
    closeEdit () {
      // 关闭编辑弹出层   回复已选频道原始状态
      this.show = false
      console.log(this.$refs.channel)
      console.log(this.$refs.nav)
      this.$refs.channel.isShow = false
    },
    async updateChannelFn (userChannelList) {
      // 更新用户的频道
      let arr = JSON.parse(JSON.stringify(userChannelList)) // 深拷贝
      arr = arr.map((obj, index) => {
        delete obj.name
        obj.seq = index + 1
        return obj
      })
      arr.splice(0, 1)
      await undateChannelAPI({
        channels: arr
      })
    }
  },
  watch: {
    // 侦听active变化 显示对应tabs的数据
    active () {
      this.channelShow = this.active
      this.articleList = []
      this.page = 1
      this.onLoad()
    }
  }
}
</script>

<style scoped lang="less">
.logo {
  width: 115px;
  height: 37px;
}
.top {
  padding-top: 46px;
}
// 设置 tabs 容器的样式
/deep/ .van-tabs__wrap {
  padding-right: 30px;
  background-color: #fff;
}

// 设置小图标的样式
.moreChannels {
  position: fixed;
  top: 62px;
  right: 8px;
  z-index: 999;
}

// 弹出层容器宽度高度铺满屏幕
/*频道编辑弹出层 */
.edit_wrap {
  width: 100%;
  height: 100%;
}
</style>
