<template>
  <div class="user-edit-container">
    <!-- Header 区域 -->
    <van-nav-bar
      title="编辑资料"
      left-arrow
      @click-left="$router.back()"
      fixed
    />

    <!-- 用户资料 -->
    <van-cell-group class="action-card">
      <van-cell title="头像" is-link center>
        <template #default>
          <van-image
            round
            class="avatar"
            :src="userProfile.photo"
            @click="avatatFn"
          />
          <!-- file 选择框 -->
          <input type="file" ref="iptFile" v-show="false" @change="changeFn" />
        </template>
      </van-cell>

      <van-cell
        title="名称"
        is-link
        :value="userProfile.name"
        @click="changeNameFn"
      />

      <van-cell
        title="生日"
        is-link
        :value="userProfile.birthday"
        @click="timeShow = true"
      />
    </van-cell-group>
    <!-- 姓名弹出层 -->
    <van-dialog
      v-model="show"
      title="修改名称"
      show-cancel-button
      :before-close="closeFn"
    >
      <van-field
        v-focus
        v-model="value"
        placeholder="请输新的名称"
        input-align="center"
      />
    </van-dialog>
    <!-- 时间选择 -->
    <van-popup
      v-model="timeShow"
      round
      position="bottom"
    >
      <van-datetime-picker
        v-model="currentDate"
        type="date"
        title="选择出生年月日"
        :min-date="minDate"
        :max-date="maxDate"
        @confirm='updataTime'
        @cancel='timeShow=false'
      />
    </van-popup>
  </div>
</template>

<script>
import {
  getUserProfileAPI,
  changeUserAvatatrAPI,
  changeUserProfileAPI
} from '@/api'
import Notify from '@/ui/Notify'
import moment from 'moment'
export default {
  data () {
    return {
      userProfile: {}, // 用户个人资料
      show: false, // 控制修改名称弹出框显示
      value: '', // 输入框内容
      timeShow: false, // 控制选择时间弹框
      minDate: new Date(1900, 0, 0), // 最小时间范围
      maxDate: new Date(), // 最大时间范围(今天)
      currentDate: new Date() // 当前时间(将来获取完数据, 再设置给默认选中的值)
    }
  },
  async created () {
    // 获取个人资料
    const [, res] = await getUserProfileAPI()
    this.userProfile = res.data.data
  },
  methods: {
    avatatFn () {
      // 点击头像事件
      this.$refs.iptFile.click()
    },
    async changeFn (e) {
      // input选择文件事件
      const fd = new FormData()
      fd.append('photo', e.target.files[0])
      const [, res] = await changeUserAvatatrAPI(fd)
      this.userProfile.photo = res.data.data.photo
    },
    changeNameFn () {
      // 点击修改名称是触发
      this.show = true
      this.value = this.userProfile.name
    },
    async closeFn (action, done) {
      if (action === 'cancel') done()
      else {
        if (/^[a-zA-Z\u4e00-\u9fa5]{1,7}$/.test(this.value)) {
          // 验证用户名是否规范
          if (this.userProfile.name !== this.value) {
            // 新名称合格执行
            await changeUserProfileAPI({
              name: this.value
            })
            Notify({ type: 'success', message: '修改成功' })
            this.userProfile.name = this.value
          } else {
            // 名称重复的提示
            Notify({ type: 'danger', message: '用户名重复' })
            done(false)
          }
          done()
        } else {
          // 输入不规范提示
          Notify({ type: 'warning', message: '请输入1~7位中英文' })
          done(false)
        }
      }
    },
    async updataTime () {
      this.currentDate = moment(this.currentDate).format('YYYY-MM-DD')
      const [, res] = await changeUserProfileAPI({
        birthday: this.currentDate
      })
      this.userProfile.birthday = res.data.data.birthday
      this.timeShow = false
    }
  }
}
</script>

<style lang="less" scoped>
.user-edit-container {
  padding-top: 46px;
  .avatar {
    width: 50px;
    height: 50px;
  }
}
</style>
